.sidebar {
    position: fixed;
    border: 1px;
    top: 0;
    left: -317px;
    width: 317px;
    height: 100vh;
    background: white;
    z-index: 101;
    display: flex;
    flex-direction: column;
    transition: 0.4s ease-in-out;
}

.sidebar.open {
    left: 0
}

.background {
    position: fixed;
    background-color: #181818;
    opacity: 0.2;
    border: 10px;
    inset: 0;
    z-index: 100;
}

.nav {
    margin-top: 63px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 45px;
}

.nav a {
    color: #000000;
    font-family: Montserrat;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-style: solid;
    text-decoration-skip-ink: none;
    text-decoration-thickness: auto;
    text-underline-offset: auto;
    text-underline-position: from-font;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;


}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;

}

.close {
    position: absolute;
    top: 16px;
    right: 16px;
    background: white;
    border: 0;
    transition: transform 0.3s ease;
}
.close:active{
    background: #d2e3d6;
    border-radius: 15px;
    transform: rotate(7200deg);
    transition: transform 0.3s ease;

}

